@import '../../global/global.css';

:host {
  display: inline-block;
  position: relative;
}

.date-picker-container {
  position: relative;
  display: inline-block;
}

.date-picker-trigger {
  cursor: pointer;
}

.date-picker-trigger.input-field-container {
  position: relative;
}

.calendar-icon-overlay {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 100%;
  cursor: pointer;
  z-index: 1;
}

.date-picker-menu-container {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  margin-top: var(--spacing-xs);
}

.date-picker-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: transparent;
}
