import { Component, Prop, State, Event, EventEmitter, Watch, h, Host } from '@stencil/core';
import { SelectedDate, DisabledDate } from '../bh-date-picker-content/bh-date-picker-content';

export type DatePickerVariant = 'dropdown-outlined' | 'dropdown-ghost' | 'input';

@Component({
  tag: 'bh-date-picker',
  styleUrl: 'bh-date-picker.css',
  shadow: true,
})
export class BhDatePicker {
  /**
   * Variant of the date picker trigger
   */
  @Prop() variant: DatePickerVariant = 'input';

  /**
   * Minimum year in the picker
   */
  @Prop() minYear: number = 1998;

  /**
   * Maximum year in the picker
   */
  @Prop() maxYear: number = 2060;

  /**
   * Whether the picker is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Array of disabled dates
   */
  @Prop() disabledDates: DisabledDate[] = [];

  /**
   * Label for input variant
   */
  @Prop() label: string = 'Select Date';

  /**
   * Placeholder text
   */
  @Prop() placeholder: string = 'Choose date';

  /**
   * Width for input variant
   */
  @Prop() inputWidth: string = '200px';

  /**
   * Whether to show the label
   */
  @Prop() showLabel: boolean = true;

  /**
   * Whether to show the footer
   */
  @Prop() showFooter: boolean = false;

  /**
   * Whether to show days from other months
   */
  @Prop() showOtherMonthDays: boolean = true;

  /**
   * Currently selected value
   */
  @Prop() value?: SelectedDate;

  @State() selectedValue: SelectedDate | null = null;
  @State() tempSelection: SelectedDate | null = null;
  @State() isMenuOpen: boolean = false;
  @State() isMenuClosing: boolean = false;

  /**
   * Event emitted when value changes
   */
  @Event() bhChange!: EventEmitter<SelectedDate | null>;

  /**
   * Event emitted when a date is selected
   */
  @Event() bhDateSelect!: EventEmitter<SelectedDate | null>;

  @Watch('value')
  handleValueChange(newValue: SelectedDate | undefined) {
    if (newValue !== undefined) {
      this.selectedValue = newValue;
    }
  }

  componentWillLoad() {
    if (this.value) {
      this.selectedValue = this.value;
    }
  }

  private getDisplayValue(): string {
    if (!this.selectedValue) return '';
    const month = (this.selectedValue.month + 1).toString().padStart(2, '0');
    const day = this.selectedValue.day.toString().padStart(2, '0');
    return `${month}/${day}/${this.selectedValue.year}`;
  }

  private getButtonLabel(): string {
    return this.getDisplayValue() || this.placeholder;
  }

  private handleTriggerClick = () => {
    if (this.disabled) return;
    if (!this.isMenuOpen && this.showFooter) {
      this.tempSelection = this.selectedValue;
    }
    this.isMenuOpen = !this.isMenuOpen;
  };

  private handleInputFocus = () => {
    if (this.disabled) return;
    if (!this.isMenuOpen && this.showFooter) {
      this.tempSelection = this.selectedValue;
    }
    this.isMenuOpen = true;
  };

  private handleDateSelect = (event: CustomEvent<SelectedDate | null>) => {
    const date = event.detail;
    if (this.showFooter && date) {
      this.tempSelection = date;
    } else {
      this.selectedValue = date;
      this.bhChange.emit(date);
      this.bhDateSelect.emit(date);
      if (!this.showFooter) {
        this.closeMenu();
      }
    }
  };

  private handleMenuCancel = () => {
    this.tempSelection = null;
    this.closeMenu();
  };

  private handleMenuApply = () => {
    if (this.tempSelection) {
      this.selectedValue = this.tempSelection;
      this.tempSelection = null;
      this.bhChange.emit(this.selectedValue);
      this.bhDateSelect.emit(this.selectedValue);
    }
    this.closeMenu();
  };

  private handleToday = () => {
    const today = new Date();
    const todayDate: SelectedDate = {
      year: today.getFullYear(),
      month: today.getMonth(),
      day: today.getDate(),
    };

    if (this.showFooter) {
      this.tempSelection = todayDate;
    } else {
      this.selectedValue = todayDate;
      this.bhChange.emit(todayDate);
      this.bhDateSelect.emit(todayDate);
      this.closeMenu();
    }
  };

  private closeMenu() {
    this.isMenuClosing = true;
    setTimeout(() => {
      this.isMenuOpen = false;
      this.isMenuClosing = false;
    }, 150);
  }

  private handleBackdropClick = () => {
    this.handleMenuCancel();
  };

  render() {
    return (
      <Host>
        <div class="date-picker-container">
          {/* Dropdown Outlined Variant */}
          {this.variant === 'dropdown-outlined' && (
            <div class="date-picker-trigger" onClick={this.handleTriggerClick}>
              <bh-button
                hierarchy="secondary"
                
                size="sm"
                icon="trailing"
                label={this.getButtonLabel()}
                iconName="expand_more"
                disabled={this.disabled}
              ></bh-button>
            </div>
          )}

          {/* Dropdown Ghost Variant */}
          {this.variant === 'dropdown-ghost' && (
            <div class="date-picker-trigger" onClick={this.handleTriggerClick}>
              <bh-button
                hierarchy="tertiary"
                
                size="sm"
                icon="trailing"
                label={this.getButtonLabel()}
                iconName="expand_more"
                disabled={this.disabled}
              ></bh-button>
            </div>
          )}

          {/* Input Variant */}
          {this.variant === 'input' && (
            <div class="date-picker-trigger input-field-container">
              <bh-input-text
                label={this.label}
                placeholder={this.placeholder}
                value={this.getDisplayValue()}
                disabled={this.disabled}
                showTrailingIcon={true}
                trailingIcon="calendar_today"
                showHelpIcon={false}
                showHintText={false}
                showLabel={this.showLabel}
                width={this.inputWidth}
                onBhFocus={this.handleInputFocus}
              ></bh-input-text>
              <div class="calendar-icon-overlay" onClick={this.handleTriggerClick}></div>
            </div>
          )}

          {/* Date Picker Menu */}
          <div class="date-picker-menu-container">
            <bh-picker-menu
              visible={this.isMenuOpen}
              showFooter={this.showFooter}
              showTodayButton={this.showFooter}
              disableApply={this.showFooter && this.tempSelection === null}
              onBhCancel={this.handleMenuCancel}
              onBhApply={this.handleMenuApply}
              onBhToday={this.handleToday}
            >
              <bh-date-picker-content
                minYear={this.minYear}
                maxYear={this.maxYear}
                disabled={this.disabled}
                disabledDates={this.disabledDates}
                showOtherMonthDays={this.showOtherMonthDays}
                value={this.tempSelection ?? this.selectedValue ?? undefined}
                onBhDateSelect={this.handleDateSelect}
              ></bh-date-picker-content>
            </bh-picker-menu>
          </div>
        </div>

        {/* Backdrop */}
        {(this.isMenuOpen || this.isMenuClosing) && (
          <div class="date-picker-backdrop" onClick={this.handleBackdropClick}></div>
        )}
      </Host>
    );
  }
}
